import styled from 'styled-components'
import { variables } from '../../styles/variables';

export const Ruling = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  background-image: url(${props => props.image? props.image : ''});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  font-size: 16px;
  height: 550px;
  position: relative;
  .ruling {
    padding: 35px 60px 35px 35px;
    color: white;
    width: calc(100% - 95px);
    position: relative;
    z-index: 20;
     &__title {
      position: relative;
      &__state {
        position: absolute;
        left: -35px;
        top: 10px;
        width: 40px;
        height: 40px;
      }
      &__name {
        font-size: 2.6em;
        font-weight: 600;
      }
    }

    &__experience {
      font-size: 0.82em;
      font-weight: 600;
      margin: 0px;
    }

    &__message {
      font-size: 1em;
      font-weight: 400;
      margin: 16px 0px;
      height: 38px;
    }

    &__actions {
      margin-bottom: 2px;
      width: 45%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }

  }
  .message {
    flex: auto;
    text-align: left;
    font-size: 15px;
    font-weight: 200;
  }
  .close {
    cursor: pointer;
    width: 30px;
    height: 30px;
  }

  .filter {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    background: rgba(0,0,0,0);
    background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 17%, rgba(0,0,0,0.75) 100%);
    background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(0,0,0,0)), color-stop(17%, rgba(0,0,0,0)), color-stop(100%, rgba(0,0,0,0.75)));
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 17%, rgba(0,0,0,0.75) 100%);
    background: -o-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 17%, rgba(0,0,0,0.75) 100%);
    background: -ms-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 17%, rgba(0,0,0,0.75) 100%);
    background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,0) 17%, rgba(0,0,0,0.75) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000', GradientType=0 );
    z-index: 10;
  }
`;





